.MoviesOrTv {
  display: flex;
  justify-content: space-around;
  padding: 10rem 0;
}
.MoviesOrTv .btn {
  padding: 1rem 10rem;
  font-weight: 500;
  font-size: 1.9rem;
  letter-spacing: 0.25rem;
  color: #ddd;
  border: 2px solid #999;
  background: #1b242f !important;
}
.MoviesOrTv .btn:hover {
  background: #1b242f !important;
  border-color: #ddd !important;
}
.MoviesOrTv .btn:focus {
  box-shadow: none !important;
  padding: 1rem 10rem;
}
.MoviesOrTv .btn-selected {
  border: 2px solid #b31b20;
}
.MoviesOrTv .btn-selected:hover {
  border-color: #ed1c24 !important;
  background-color: #1b242f !important;
}

/* MEDIA QUERIES */
/* Extra small  */
@media (max-width: 575.98px) {
  .MoviesOrTv {
    padding: 5rem 3rem 7.5rem !important;
  }
  .MoviesOrTv .btn,
  .MoviesOrTv .btn.btn-selected,
  .MoviesOrTv .btn:focus {
    padding: 1rem 2rem;
    letter-spacing: 0.2rem;
    font-size: 2rem;
  }
}

/* Small  */
@media (min-width: 576px) and (max-width: 767.98px) {
  .MoviesOrTv.container {
    padding: 10rem 0;
  }
  .MoviesOrTv .btn,
  .MoviesOrTv .btn.btn-selected,
  .MoviesOrTv .btn:focus {
    padding: 1rem 5rem;
    letter-spacing: 0.2rem;
    font-size: 2rem;
  }
}

/* Medium  */
@media (min-width: 768px) and (max-width: 991.98px) {
  .MoviesOrTv.container {
    padding: 10rem 0;
  }
  .MoviesOrTv .btn,
  .MoviesOrTv .btn.btn-selected,
  .MoviesOrTv .btn:focus {
    padding: 1rem 5rem;
    letter-spacing: 0.2rem;
    font-size: 2rem;
  }
}

/* Large  */
@media (min-width: 992px) and (max-width: 1199.98px) {
  .MoviesOrTv.container {
    padding: 10rem 0;
  }
}

