.main_container {
  padding-bottom: 5rem;
  margin-bottom: 5rem;
}
.main_container:not(:last-child) {
  border-bottom: 2px solid #1b242f;
}
.container-header--title {
  text-transform: uppercase;
  margin-bottom: 3rem;
  padding-bottom: 1rem;
  font-weight: 500;
  letter-spacing: 0.25rem;
  font-size: 2.5rem;
  position: relative;
  color: #fff;
}
.container-header--title::after {
  content: '';
  display: block;
  background: #fff;
  width: 25rem;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 1px;
}
.content .main_container .slick-track {
  display: flex !important;
  align-content: center;
  margin: 0 !important;
  position: relative;
}
.content .main_container .slick-prev {
  left: 95.5% !important;
  top: -30% !important;
}
.content .main_container .slick-next {
  left: 97.5% !important;
  top: -30% !important;
}
.content .main_container .slick-prev,
.content .slick-next {
  transform: translate(-50%, -50%) !important;
}
.content .main_container .slick-prev::before {
  content: '❮' !important;
  color: #fff !important;
  opacity: 1 !important;
  font-size: 3rem !important;
  transition: all 0.25s linear;
}
.content .main_container .slick-next::before {
  content: '❯' !important;
  color: #fff !important;
  opacity: 1 !important;
  font-size: 3rem !important;
  transition: all 0.25s linear;
}
.content .main_container .slick-arrow {
  font-size: 0 !important;
  width: 0 !important;
  height: 0 !important;
}
.content .main_container .slick-arrow:hover {
  color: var(--main-red) !important;
}
.content .main_container .slick-prev:hover::before,
.content .main_container .slick-next:hover::before {
  cursor: pointer;
  color: var(--main-red) !important;
}
.content .main_container .container-content .slick-slide {
  width: 200px !important;
}
.content .main_container .item {
  border: 2px solid #252934;
  height: 24.5rem;
  width: 15rem !important;
  background-color: #252934;
  position: relative;
}
.content .main_container .item a {
  width: 100%;
  height: 21.2rem;
  position: absolute;
  top: 0;
}
.content .main_container .item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.5;
  transition: all 0.25s linear;
}
.content .main_container .item img:hover {
  opacity: 1;
}
.content .main_container .item-footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  font-size: 1.75rem;
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
}
.content .main_container .item-footer p {
  margin-bottom: 0;
  color: #ddd;
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.content .main_container .item-footer p span {
  display: flex;
  align-items: center;
}
.content .main_container .item-footer p span:first-child svg {
  color: var(--main-red);
  margin-right: 0.5rem;
  opacity: 0.65;
  margin-top: 0.25rem;
}
.content .main_container .item-footer p span:nth-child(2) svg {
  color: gold;
  margin-right: 0.5rem;
}

/* MEDIA QUERIES */
/* Extra small  */
@media (max-width: 575.98px) {
  .content.container {
    padding: 0 24px !important;
  }
  .content .main_container .container-content .slick-slide {
    width: 156px !important;
  }
  .content .main_container .slick-prev {
    left: 86% !important;
    top: -30% !important;
  }
  .content .main_container .slick-next {
    left: 92% !important;
    top: -30% !important;
  }
}

/* Small  */
@media (min-width: 576px) and (max-width: 767.98px) {
  .content .main_container .container-content .slick-slide {
    width: 170px !important;
  }
  .content .main_container .slick-prev {
    left: 94.5% !important;
    top: -30% !important;
  }
  .content .main_container .slick-next {
    left: 98% !important;
    top: -30% !important;
  }
}

/* Medium  */
@media (min-width: 768px) and (max-width: 991.98px) {
  .content.container {
    padding: 0 24px !important;
  }
  .content .main_container .slick-prev {
    left: 94.5% !important;
    top: -27% !important;
  }
  .content .main_container .slick-next {
    top: -27% !important;
  }
}

/* Large  */
@media (min-width: 992px) and (max-width: 1199.98px) {
  .content.container {
    padding: 0 24px !important;
  }
